import { errUnknown } from "./common_legacy";
import { launchTroopPlugin } from "./ext.plugin_troop";

import "../scss/troop.scss";

jQuery(function($){
    //btnJoinTroop, btnLeaveTroop, btnKickTroop, btnCreateTroop, btnChangeTroopName
    $('#btnLeaveTroop').click(function(e){
        if(!confirm("  ?")){
            return false;
        }
        $.post({
            url:'j_troop.php',
            dataType:'json',
            data:{
                action:''
            }
        }).then(function(data){
            console.log(data);
            if(!data.result){
                alert(data.reason);
                location.reload();
            }
    
            location.reload();
    
        }, errUnknown);
        return false;
    });

    $('#btnCreateTroop').click(function(e){
        $.post({
            url:'j_troop.php',
            dataType:'json',
            data:{
                action:'',
                name:$('#nameplate').val()
            }
        }).then(function(data){
            console.log(data);
            if(!data.result){
                alert(data.reason);
                location.reload();
            }
    
            location.reload();
    
        }, errUnknown);
        return false;
    });

    $('#btnChangeTroopName').click(function(e){
        $.post({
            url:'j_troop.php',
            dataType:'json',
            data:{
                action:'',
                name:$('#nameplate').val()
            }
        }).then(function(data){
            console.log(data);
            if(!data.result){
                alert(data.reason);
                location.reload();
            }
    
            location.reload();
    
        }, errUnknown);
        return false;
    });

    $('#btnKickTroop').click(function(e){
        $.post({
            url:'j_troop.php',
            dataType:'json',
            data:{
                action:'',
                gen:$('#genNo').val()
            }
        }).then(function(data){
            console.log(data);
            if(!data.result){
                alert(data.reason);
                location.reload();
            }
    
            location.reload();
    
        }, errUnknown);
        return false;
    });

    $('#btnJoinTroop').click(function(e){
        $.post({
            url:'j_troop.php',
            dataType:'json',
            data:{
                action:'',
                troop:$('.troopId:checked').val()
            }
        }).then(function(data){
            console.log(data);
            if(!data.result){
                alert(data.reason);
                location.reload();
            }
    
            location.reload();
    
        }, errUnknown);
        return false;
    });

    
    launchTroopPlugin($);
});